.button a {
    text-transform: uppercase;
    color: #303030;
    text-decoration: none;
    outline: 0;
    border: 2px solid #303030;
    padding:10px 20px;
    background-color: transparent;
    font-family: Raleway,sans-serif;
    font-weight: 700;
}

.button a:hover {
    color: #fff;
}




